:root {
  --bs-font-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --bs-body-font-family: var(--bs-font-sans-serif);
}

.container {
  display: flex;
  flex-direction: column;
  height: 98vh;
  width: 800px;
  margin-left: auto;
  margin-right: auto;
}

body {
  background-color: #1c1c1e;
  color: #FFFFFF;
  font-family: var(--bs-body-font-family);
  font-weight: 200 !important;
}

h2, a {
  color: #FFFFFF;
  text-decoration: none;
}

h2 {
  font-weight: 400;
}

h3 {
  font-size: 22px;
  margin-bottom: 7px;
}

@media screen and (max-width: 840px) {
  .container {
    width: 95%;
  }

  .top {
    width: 100% !important;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 90%;
  }
}

@media screen and (max-width: 430px) {
  html {
    font-size: 80%;
  }
}

@media screen and (max-width: 370px) {
  html {
    font-size: 70%;
  }
}

@media screen and (max-width: 330) {
  html {
    font-size: 50%;
  }
}

.top {
  width: 800px;
  display: flex;
  margin-bottom: -20px;
}

.header {
  padding-top: 1.5rem;
  flex: 1;
}

.navbar {
  padding-top: 1.5rem;
}

.navbar-list {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.navbar-item {
  cursor: pointer;
  margin-left: 13px;
}

.content {
  margin-top: 30px;
}

.left-content {
  flex: 1;
  padding: 1rem;
  width: 50%;
  overflow-y: scroll;
  border-top: 1px solid black;
  border-radius: 1px;
}

.right-content {
  flex: 1;
  padding: 1rem;
  width: 50%;
  overflow-y: scroll;
  border-top: 1px solid black;
  border-left: 1px solid black;
  border-radius: 1px;
}

.left-content div {
  padding: 10px;
}

h2 {
  margin-top: 0;
}


.experience {
  margin-bottom: 20px;
  border-radius: 3px;
}

.experience a {
  color: #0070f3;
  text-decoration: none;
}

.exp-row1, .exp-row2 {
  display: flex;
  padding: 2px;
}

.exp-row1 a {
  text-decoration: none;
  color: rgb(10,132,255);
}

#position, #tools {
  color: rgb(142,142,147);
}

.exp-row3 div {
  padding: 2px;
}

.profile-pic {
  width:700px;
  height: 500px;
}

#life {
  text-align: center;
}

.achievement-list {
  padding-left: 10px; /* Adjust this value to control the indentation of the achievements */
}

.achievement-list div {
  margin-left: 10px;
  text-indent: -10px; /* Adjust this value to align the text of achievements with the text above */
}

.achievement a{
  color: #FFFFFF !important;
}


.profile img {
  width: 150px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.profile-des {
  text-align: center;
  padding-bottom: 0.5rem;
}

.profile-des h2, .profile-des p {
  margin: 5px;
}
